body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.layout-background {
  background: #fff;
  border-radius: .5rem;
  box-shadow: 0 1px 15px rgb(27 31 35 / 15%) !important;
}

.hidden {
  display: none;
}

.invisible {
  visibility: hidden;
  transition: none;
}

.odr-mgr-main-layout {
  display: flex;
  min-height: 100vh !important;
}

.odr-mgr-page-spinner {
  position: absolute !important;
  top: 50% !important;
  left: 50% !important;
  margin-right: -50% !important;
  transform: translate(-50%, -50%) !important;
}
.odr-mgr.ant-menu.ant-menu-light.ant-menu-root {
    margin: 12px 0px;
}

.odr-mgr .ant-menu-item.ant-menu-item-only-child {
    margin: 0;
}

.odr-mgr.ant-divider-horizontal.ant-divider-with-text-left::before {
    top: 50%;
    width: 2%;
}

.odr-mgr.ant-breadcrumb {
    margin: 16px 0px;
}

.odr-mgr.ant-form.ant-form-vertical .ant-form-item-label {
    padding-top: 4px;
    padding-bottom: 4px;
    font-weight: 600;
}

.odr-mgr>.ant-space-item:first-child {
    width: 100%;
}

/* .odr-mgr .ant-form-item-has-error .ant-form-item-label > label{
    color: #ff4d4f;
} */

/* .odr-mgr .ant-form-item-with-help {
   margin-bottom: 10px;
} */

.odr-mgr.ant-steps .ant-steps-item-title {
    font-weight: 600;
}

.ant-upload.ant-upload-select.ant-upload-select-picture-card>.ant-upload:hover:not(.ant-upload-disabled) {
    color: #1890ff;
    transition: all 0.3s;
}

.ant-upload.ant-upload-select.ant-upload-select-picture-card>.ant-upload.ant-upload-disabled {
    color: rgba(0, 0, 0, 0.25);
    transition: all 0.3s;
}
