.odr-mgr.ant-menu.ant-menu-light.ant-menu-root {
    margin: 12px 0px;
}

.odr-mgr .ant-menu-item.ant-menu-item-only-child {
    margin: 0;
}

.odr-mgr.ant-divider-horizontal.ant-divider-with-text-left::before {
    top: 50%;
    width: 2%;
}

.odr-mgr.ant-breadcrumb {
    margin: 16px 0px;
}

.odr-mgr.ant-form.ant-form-vertical .ant-form-item-label {
    padding-top: 4px;
    padding-bottom: 4px;
    font-weight: 600;
}

.odr-mgr>.ant-space-item:first-child {
    width: 100%;
}

