.odr-mgr.ant-steps .ant-steps-item-title {
    font-weight: 600;
}

.ant-upload.ant-upload-select.ant-upload-select-picture-card>.ant-upload:hover:not(.ant-upload-disabled) {
    color: #1890ff;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}

.ant-upload.ant-upload-select.ant-upload-select-picture-card>.ant-upload.ant-upload-disabled {
    color: rgba(0, 0, 0, 0.25);
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}